<template>
  <div>
    <div class="container1 fl-ex fl-co ju-be">
      <div class="pie" ref="chart"></div>
      <div class="sign">
        <div class="sign1 fl-ex al-it ju-be">
          <div style="background-color:#ffa332;"></div>
          <p>
            慈善机构:
            <span>300</span>家
          </p>
          <div style="background-color:#2ef2e8;"></div>
          <p>
            社会机构:
            <span>150</span>家
          </p>
        </div>
        <div class="sign1 fl-ex al-it ju-be">
          <div style="background-color:#81bcff;"></div>
          <p>
            行业协会:
            <span>150</span>家
          </p>
          <div style="background-color:#67c259;"></div>
          <p>
            学术组织:
            <span>150</span>家
          </p>
        </div>
        <div class="sign1 fl-ex al-it">
          <div style="background-color:#fe9ea0;"></div>
          <p style="margin-left:1rem;">
            基金会:
            <span>150</span>家
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const echarts = require("echarts");
export default {
  data() {
    return {};
  },
  methods: {
    initCharts() {
      let myChart = echarts.init(this.$refs.chart);
      myChart.setOption({
        tooltip: {
        },
        calculable: true,
        series: [
          {
            color: [
              "#ffa336", //黄色
              "#2ef2e8", //青色
              "#67c259", //绿色
              "#81bcff", //蓝色
              "#fe9ea0", //粉色
            ],
            type: "pie",
            radius: [30, 125],
            center: ["50%", "55%"],
            roseType: "radius",
            label: {
              normal: {
                show: false
              },
              emphasis: {
                show: true
              }
            },
            lableLine: {
              normal: {
                show: false
              },
              emphasis: {
                show: true
              }
            },
            data: [
              { value:20, name: "慈善机构300家" },
              { value:15, name: "社会机构150家" },
              { value:15, name: "学术组织150家" },
              { value:15, name: "行业协会150家" },
              { value:15, name: "基金会150家" }
            ]
          }
        ]
      });
    }
  },
  mounted() {
    this.initCharts();
  }
};
</script>

<style scoped>
.container1 {
  width: 100%;
  height: 25.5rem;
}
.container1 .pie {
  width: 90%;
  height: 19rem;
  margin: 0 auto;
}
.container1 .sign {
  width: 76%;
  height: 4.5rem;
  margin: 0 auto;
}
.container1 .sign .sign1 {
  width: 100%;
  height: 1.5rem;
}
.container1 .sign .sign1 > div {
  width: 1.2rem;
  height: 0.5rem;
}
.container1 .sign .sign1 > p {
  font-size: 0.85rem;
  color: black;
  text-align: left;
}
</style>


